/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin        : 0;
    padding       : 0;
    border        : 0;
    font-size     : 100%;
    font          : inherit;
    vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

body {
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing : 0;
}

.loader {
    border       : 16px solid #f3f3f3;
    border-top   : 16px solid #3498db;
    border-radius: 50%;
    width        : 120px;
    height       : 120px;
    animation    : spin 2s linear infinite;
    margin       : 0 auto;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

html, body {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

header {
    section {
        display: flex;
        align-items: center;
        padding:0.75rem 0.5rem;
        background: linear-gradient(180deg, rgba(orange, 80%), rgba(orange, 50%));
        border-bottom:1px solid #ddd;
        h1 {
            flex-grow: 1;
            font-size:1.5em;
        }
        input {
            flex-shrink: 0;
            font-size:1.25em;
        }
    }
}

ul.movies {
    padding:1em 0;
    max-width: 50em;
    margin:0 auto;

    li + li {
        margin-top: 1em;
    }
    article {
        display: flex;
        align-items: flex-start;
        padding:0 1rem;

        em {
            background-color: yellow;
        }

        h2 {
            font-size:1.75em;
        }

        p {
            margin-bottom:0.35em;
            color:#666;
            font-size:0.95em;
        }
    
        img {
            flex-shrink: 0;
            margin: 0.75em 1em 0 0;
            border:1px solid #aaa;
            box-shadow: 4px 4px 6px -4px rgba(0,0,0,0.75);
            &.empty {
                width:92px;
            }
        }
    
        >.content {
            flex-grow: 1;
            line-height: 1.5;
        }
    
        dl {
            display: grid;
            row-gap: 0.35em;
            grid-template-columns: 10em 1fr;
            dt {
                grid-column: 1;
                color:#333;
            }
            dd {
                grid-column: 2;
            }
        }
    }
}

.range {
    width:100px;
    height:1em;
    border:1px solid #ddd;
    position: relative;
    background: linear-gradient(90deg, red, orange);
    border-radius: 3px;

    >.amount {
        position: absolute;
        right:0;
        top:0;
        bottom:0;
        background: white;
    }
    >.value {
        text-align: center;
        position: absolute;
        right:0;
        top:0;
        bottom:0;
        left:0;
        z-index: 2;
        font-size:0.75em;
    }
}

footer {
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(180deg, rgba(orange, 50%), rgba(orange, 80%));
    >img {
        height:50px;
    }
}

p.nothing-found {
    margin:1em 0.5em;
    font-size:1.25em;
}